<template>
  <div class="register">
    <div class="Form_Box">
      <div class="logo">
        <Logo :size="100"/>
      </div>
      <div class="Border_Top_Box">
        <h4 class="reg_text">Register</h4>
        <el-form label-position="top" ref="registerForm" :rules="rules" label-width="80px" :model="formLabelAlign" class="form">
          <el-form-item label="Email" prop="email">
            <el-input v-model="formLabelAlign.email"></el-input>
          </el-form-item>
          <!-- <el-form-item label="Email Code" prop="email_code">
            <el-col :md="20" :xs="15" :sm="18">
              <el-input v-model="formLabelAlign.email_code" placeholder="please click button at right to send code to your email"></el-input>
            </el-col>
            <el-col :md="4" :xs="9" :span="6">
              <el-button type="primary" icon="el-icon-message">Send Code</el-button>
            </el-col>
          </el-form-item> -->
          <el-form-item label="Password" prop="password">
            <el-input type="password" v-model="formLabelAlign.password"></el-input>
          </el-form-item>
          <el-form-item label="Password Confirmtation" prop="password2">
            <el-input type="password" v-model="formLabelAlign.password2"></el-input>
          </el-form-item>
          <el-divider content-position="left">Invitation Code</el-divider>
          <el-form-item prop="invite_code">
            <el-input v-model="formLabelAlign.invite_code"></el-input>
          </el-form-item>
          <el-form-item prop="checked">
            <el-checkbox v-model="formLabelAlign.checked">I agree with the terms and conditions</el-checkbox>
          </el-form-item>
          <el-button type="primary" :loading="loading" class="Form_Submit" @click="handleSubmit('registerForm')" size="nomal">Register</el-button>
        </el-form>
      </div>
      <Footer></Footer>
    </div>
  </div>
</template>
<script>
import md5 from 'js-md5'
import { user_register } from '@/api'
export default {
  data () {
    const pwd2Check = (rule, value, callback) => {
      const { password, password2 } = this.formLabelAlign
      if (password !== password2) {
        return callback(new Error('两次输入密码不一致!'))
      }
      callback()
    }
    return {
      formLabelAlign: {},
      loading: false,
      rules: {
        email: [
          { required: true, message: '请输入Email', trigger: 'blur' },
        ],
        email_code: [
          { required: true, message: '请输入Email Code', trigger: 'blur' },
        ],
        password: [
          { required: true, message: '请输入Password', trigger: 'blur' },
        ],
        password2: [
          { required: true, message: '请确认Password', trigger: 'blur' },
          { validator: pwd2Check, trigger: 'blur' }
        ],
        checked: [
          { required: true, message: '请勾选', trigger: 'change' },
        ],
      }
    }
  },
  methods: {
    handleSubmit (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          this.loading = true
          const { email: username, password, invite_code } = this.formLabelAlign
          const [res] = await user_register({
            username,
            pwd: md5(password),
            invite_code
          })
          this.loading = false
          if (res) {
            this.$message('注册成功，请登录')
            setTimeout(() => {
              this.$router.push('/login')
            }, 2500)
          }
        } else {
          console.log('error submit!!');
          return false;
        }
      })
    }
  }
}
</script>
<style lang="less" scoped>
@import url('../../assets/css/var.less');
.register {
  background: @reg_bg;
  min-height: 100vh;
  padding-bottom: 40px;
  .logo {
    margin: 50px auto;
  }
  .reg_text {
    color: @color;
  }
  .form {
    margin-top: 40px;
  }
}
</style>